<template>
    <div>
        <el-row :gutter="20">
            <el-col :span="12" :offset="6">
                <el-card shadow="always">
                    <div slot="header" class="header">
                        <span>Message Board</span>
                    </div>
                    <el-form :model="messageForm" ref="messageFormRef" :rules="messageRules">
                        <el-form-item prop="nickname">
                            <el-input v-model="messageForm.nickname" placeholder="Name"></el-input>
                        </el-form-item>
                        <el-form-item prop="message">
                            <el-input class="message" type="textarea" :autosize="{ minRows: 2, maxRows: 4}"
                                      placeholder="Message" v-model="messageForm.message">
                            </el-input>
                        </el-form-item>
                        <el-button type="primary" @click="addMessage">Submit</el-button>
                    </el-form>
                </el-card>
            </el-col>
        </el-row>
        <el-row :gutter="20">
            <el-col :span="12" :offset="6">
                <el-card shadow="always">
                    <div slot="header">
                        <span>{{countTotal}} MESSAGE</span>
                    </div>
                    <div class="comment" v-for="item in data">
                        <div class="icon">
                            <el-avatar :size="50" :style="{background: colorShow}">
                                {{ item.username[0] }}
                            </el-avatar>
                        </div>
                        <div class="info">
                            <div class="user">
                                <span :style="{color: colorShow}">{{ item.username}}</span>
                                <span class="time">{{ item.addtime}}</span>
                            </div>
                            <div class="bg">
                                <span>{{ item.message}}</span>
                            </div>
                        </div>
                    </div>
                    <div class="page">
                        <el-pagination middle layout="prev, pager, next" :total="countTotal"
                                       :current-page="queryParmas.page" @current-change="handleSize"></el-pagination>
                    </div>
                </el-card>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    export default {
        name: "MessageBoard",
        components: {Comment},
        created() {
            this.randomColor()
            this.getMessage()
        },
        data() {
            return {
                colorShow: '',
                messageForm: {
                    nickname: '',
                    message: '',
                },
                queryParmas: {
                    page: 1,
                    pagesize: 10
                },
                data: [],
                countTotal: 0,
                messageRules: {
                    nickname: [
                        {required: true, message: '请输入昵称', trigger: 'blur'},
                    ],
                    message: [
                        {required: true, message: '请输入留言内容', trigger: 'blur'},
                    ]
                },
            }
        },
        methods: {
            randomColor() {
                let colorNumber = Math.random() * (2 << 23)
                let toFloor = Math.floor(colorNumber)
                this.colorShow = '#' + toFloor.toString(16)
            },
            addMessage() {
                this.$refs.messageFormRef.validate(async val => {
                    if (!val) return
                    const {data: res} = await this.$http.post('http://127.0.0.1:8000/message/board/',
                        this.messageForm)
                    console.log(res)
                    await this.getMessage()
                })
            },
            async getMessage() {
                const {data: res} = await this.$http.get('http://127.0.0.1:8000/message/messages/', {
                    params: this.queryParmas
                })
                this.data = res.data.data
                this.countTotal = res.data.total
            },
            handleSize(size) {
                this.queryParmas.page = size
                this.getMessage()
            }
        }
    }
</script>

<style lang="less" scoped>
    .el-row {
        margin-top: 20px;

        .header {
            text-align: center;
            color: #a5a5a5;
            font-size: 25px;
        }

        .el-input {
            font-size: 20px;
        }

        .message {
            font-size: 20px;
            font-family: 'Avenir', Helvetica, Arial, sans-serif;
        }

        .comment {
            margin-bottom: 5px;
            display: flex;
            align-items: center;

            .icon {
                float: left;
                margin-right: 10px;
            }

            .info {
                width: 100%;
                padding: 5px 5px;

                .user {
                    margin-bottom: 5px;
                    font-weight: bold;
                }

                .time {
                    float: right;
                }
            }

            .bg {
                margin-bottom: 10px;
                word-wrap: break-word;
                word-break: break-all;
            }
        }

        .page {
            display: flex;
            justify-content: center;
        }
    }
</style>