import Vue from 'vue'
import VueRouter from 'vue-router'
import MessageBoard from "@/views/MessageBoard";

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        redirect: '/index'
    },
    {
        path: '/index',
        component: MessageBoard,
    },
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router
